'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { Card, CardContent, Typography, Chip, Box, IconButton } from '@mui/material';
import { Favorite, FavoriteBorder } from '@mui/icons-material';
import { useRouter } from 'next/navigation';
import type { Charity } from 'lib/charities';
import { colors } from '@/theme';
import { isFavorite, toggleFavorite } from '@/utils/favorites';

interface CharityCardProps {
  charity: Charity;
}

const CharityCard: React.FC<CharityCardProps> = ({ charity }) => {
  const router = useRouter();
  const [isFav, setIsFav] = useState(false);

  useEffect(() => {
    setIsFav(isFavorite(charity.id));
  }, [charity.id]);

  const handleClick = () => router.push(`/charities/${charity.id}`);

  const handleFavoriteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    const newState = toggleFavorite(charity.id);
    setIsFav(newState);
  };

  return (
    <Card
      onClick={handleClick}
      variant="elevation"
      elevation={0}
      sx={{
        display: 'flex',
        flexDirection: 'row',
        gap: 2,
        p: 2,
        cursor: 'pointer',
        transition: 'all 0.3s ease',
        borderRadius: 4,
        position: 'relative',
        '&:hover': {
          boxShadow: 6,
          transform: 'translateY(-4px)',
        },
      }}
    >
      <IconButton
        onClick={handleFavoriteClick}
        sx={{
          position: 'absolute',
          top: 8,
          right: 8,
          zIndex: 10,
          backgroundColor: colors.white,
          boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
          '&:hover': {
            backgroundColor: colors.white,
            transform: 'scale(1.1)',
          },
          transition: 'all 0.2s ease',
        }}
        size="small"
      >
        {isFav ? (
          <Favorite
            sx={{
              fontSize: 20,
              color: colors.primary,
            }}
          />
        ) : (
          <FavoriteBorder
            sx={{
              fontSize: 20,
              color: colors.text.secondary,
            }}
          />
        )}
      </IconButton>

      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'start',
          gap: 1,
          flexShrink: 0,
        }}
      >
        <Chip
          label={charity.category}
          size="small"
          sx={{
            backgroundColor: colors.primary,
            color: colors.white,
            fontSize: '12px',
            height: { xs: 20, sm: 24 },
            borderRadius: 2.5,
          }}
        />

        <Box
          sx={{
            position: 'relative',
            width: { xs: 80, sm: 100, md: 120 },
            height: { xs: 80, sm: 100, md: 120 },
            borderRadius: 2.5,
            overflow: 'hidden',
            backgroundColor: colors.secondaryPale,
          }}
        >
          <Image
            src={`/images/foundations/${charity.image}`}
            alt={charity.title}
            fill
            priority={charity.id <= '6'}
            style={{ objectFit: 'contain' }}
            sizes="(max-width: 600px) 80px, (max-width: 900px) 100px, 120px"
          />
        </Box>
      </Box>

      <CardContent
        sx={{
          alignSelf: 'center',
          flex: 1,
          p: 0,
          pr: 4,
          display: 'flex',
          flexDirection: 'column',
          gap: 1,
          '&:last-child': { pb: 0 },
        }}
      >
        <Typography
          variant="h6"
          sx={{
            color: colors.text.primary,
            fontWeight: 600,
            fontSize: '14px',
            lineHeight: 1.3,
            display: '-webkit-box',
            WebkitLineClamp: 2,
            WebkitBoxOrient: 'vertical',
            overflow: 'hidden',
          }}
        >
          {charity.title}
        </Typography>
        <Typography
          variant="body2"
          sx={{
            color: colors.text.secondary,
            fontSize: '12px',
            lineHeight: 1.4,
            display: '-webkit-box',
            WebkitLineClamp: { xs: 2, sm: 3 },
            WebkitBoxOrient: 'vertical',
            overflow: 'hidden',
          }}
        >
          {charity.description}
        </Typography>
      </CardContent>
    </Card>
  );
};

export default CharityCard;
